import Vue from 'vue'
import Router from 'vue-router'
import ComputerSimulator from '@/components/computer-simulator'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: ComputerSimulator
    }
  ]
})



